body {
  margin: 0;
  padding: 0;
  background:black;
}


.registerUser form, .output{
    width:100vw;
    height:20vh;
    font-family: 'Courier New', Courier, monospace;
}

.registerUser form input{
    border:2px solid white;
    display:inline-block;
    position:relative;
    left:30vw;
    top:5vh;
    margin:0 10px;
    background:yellow;
    color:black;
    height:3vh;
    padding-left:10px;
}

.registerUser form input:focus{
    outline: none;
}

.output div{
    position:relative;
    margin:0 10px;
    left:30vw;
    top:5vh;
    text-align:left;
}

.uh{
    font-size: 1.6em;
    color:yellow;
}
.ue{
    color:yellow;
}
#add:hover{
    color:white;
}
#add:active{
    background:white;
}


#add{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size:1.4em;
    width:5vw;
    position:relative;
    top:5.5vh;
}


